<template>
  <div class="background">
      <div  style=" width:100%; ">
        <div class="floatCenter" style="width: 60rem">
      <!--part1-->
        <div style="height: 23rem; margin-top:5rem; text-align: left;">
          <!--<div style="font-size: xx-large; margin-top: 1.2rem;color: white">2018.12.06 coming soon</div>-->
          <div style="font-size: xx-large; margin-top: 1.2rem">BLOCK OF LIBERTY</div>
          <div style="font-size: x-large; margin-top: 1.2rem"> . </div>
          <div style="font-size: x-large; margin-top: 1.2rem">      IPO </div>
          <div style="font-size: x-large; margin-top: 1.2rem; ">        24H</div>
          <div style="font-size: x-large; margin-top: 1.2rem; ">      </div>
        </div>
      <!--part2-->
          <div style="margin-right: 1.5rem; text-align: right">
            <button @click="goTradingCenter" style="color: white; font-size: large; background: transparent; cursor: pointer; border:none;"></button>
          </div>
      <div class="floatCenter" style="height: 5rem; margin-bottom: 2rem">
          <token-head-v-m class="tokenHead" style="width: 18rem" :tokenSymbol="token1Symbol" :token-price="token1Price" ></token-head-v-m>
          <token-head-v-m class="tokenHead" style="width: 18rem" :tokenSymbol="token2Symbol" :token-price="token2Price" ></token-head-v-m>
          <token-head-v-m class="tokenHead" style="width: 18rem" :tokenSymbol="token3Symbol" :token-price="token3Price" ></token-head-v-m>
      </div>
      </div>
    </div>

    <!--pata3-->
    <div style="width:100%; height: 29rem; background: linear-gradient(to right, #B292FF, #8FBDFF)">
        <div class="floatCenter">
            <div style="text-align: left;width: 60rem; margin-top: 3rem">
            <div style="font-family: serif; font-size: xx-large"></div>
            <div style="font-family: Helvetica; font-size: large; margin-top: 2rem">   IPO </div>
            <div style="font-family: Helvetica; font-size: large; margin-top: 2rem">    500</div>
            <div style="font-family: Helvetica; font-size: large; margin-top: 2rem">  </div>
            <div style="font-family: Helvetica; font-size: large; margin-top: 2rem">    </div>
            <button @click="goApply" class="buttonComm" style="font-size: large; margin-top: 3rem"></button>
            </div>
        </div>
    </div>

    <!--pata4-->
    <div style="width:100%; height: 29rem; background: linear-gradient(to right, #8FBDFF, #B292FF)">
      <div class="floatCenter">
        <div style="text-align: right; width: 60rem; margin-top: 3rem">
          <div style="font-family: serif; font-size: xx-large"></div>
          <div style="font-family: Helvetica; font-size: large; margin-top: 2rem"></div>
          <div style="font-family: Helvetica; font-size: large; margin-top: 2rem">  </div>
          <div style="font-family: Helvetica; font-size: large; margin-top: 2rem">     </div>
          <div style="font-family: Helvetica; font-size: large; margin-top: 2rem">    </div>
          <button @click="goWriteBook" class="buttonComm" style="font-size: large; margin-top: 3rem"></button>
        </div>
      </div>
    </div>

    <!--pata5-->
    <div style="width:100%; height: 29rem; background: linear-gradient(to right, #B194FF, #50D075)">
      <div class="floatCenter">
        <div style="width: 100%; margin-top: 3rem">
          <div style="font-family: serif; font-size: xx-large">N0 ICO</div>
          <div style="font-family: Helvetica; font-size: large; margin-top: 2rem">ICO  0</div>
          <div style="font-family: Helvetica; font-size: large; margin-top: 2rem">   </div>
          <div style="font-family: Helvetica; font-size: large; margin-top: 2rem"> </div>
          <div style="font-family: Helvetica; font-size: large; margin-top: 2rem">    </div>
          <button @click="goSourceCode" class="buttonComm" style="font-size: large; margin-top: 3rem"> </button>
        </div>
      </div>
    </div>

    <!--boot-->
    <div class="excessive" style="padding-top: 2rem; padding-bottom: 5rem; border-top: 1px solid white;">
      <myFoot/>
    </div>
  </div>
</template>

<script>
import myTitle from '../components/Title'
import myFoot from './FootEx'
import tokenHeadVM from './tokenHead'
import tokenHeadjs from '../js/tokenHead'
import router from '../router'

export default {
  name: 'home',
  components: {myTitle, myFoot, tokenHeadVM},
  data: function () {
    return {
      tokens: ['SS', 'EVX', 'COVA', 'tt'] // token
    }
  },
  computed: { // store
    token1Symbol: function () {
      let tokenHeadList = this.$store.state.tokens.tokensHeadList
      if (tokenHeadList.length > 0) {
        for (let i = 0; i < tokenHeadList.length; i++) {
          if (tokenHeadList[i].symbol === this.tokens[this.tokens.indexOf('SS')]) {
            return tokenHeadList[i].symbol
          }
        }
      }
      return ''
    },
    token1Price: function () {
      let tokenHeadList = this.$store.state.tokens.tokensHeadList
      if (tokenHeadList.length > 0) {
        for (let i = 0; i < tokenHeadList.length; i++) {
          if (tokenHeadList[i].symbol === this.tokens[this.tokens.indexOf('SS')]) {
            return tokenHeadList[i].price
          }
        }
      }
      return ''
    },
    token2Symbol: function () {
      let tokenHeadList = this.$store.state.tokens.tokensHeadList
      if (tokenHeadList.length > 0) {
        for (let i = 0; i < tokenHeadList.length; i++) {
          if (tokenHeadList[i].symbol === this.tokens[this.tokens.indexOf('EVX')]) {
            return tokenHeadList[i].symbol
          }
        }
      }
      return ''
    },
    token2Price: function () {
      let tokenHeadList = this.$store.state.tokens.tokensHeadList
      if (tokenHeadList.length > 0) {
        for (let i = 0; i < tokenHeadList.length; i++) {
          if (tokenHeadList[i].symbol === this.tokens[this.tokens.indexOf('EVX')]) {
            return tokenHeadList[i].price
          }
        }
      }
      return ''
    },
    token3Symbol: function () {
      let tokenHeadList = this.$store.state.tokens.tokensHeadList
      if (tokenHeadList.length > 0) {
        for (let i = 0; i < tokenHeadList.length; i++) {
          if (tokenHeadList[i].symbol === this.tokens[this.tokens.indexOf('COVA')]) {
            return tokenHeadList[i].symbol
          }
        }
      }
      return ''
    },
    token3Price: function () {
      let tokenHeadList = this.$store.state.tokens.tokensHeadList
      if (tokenHeadList.length > 0) {
        for (let i = 0; i < tokenHeadList.length; i++) {
          if (tokenHeadList[i].symbol === this.tokens[this.tokens.indexOf('COVA')]) {
            return tokenHeadList[i].price
          }
        }
      }
      return ''
    }
  },
  watch: {
    buyInput: function () {
      this.buyInput = (this.buyInput.match(/^\d*(\.?\d{0,4})/g)[0]) || null
    },
    sellInput: function () {
      if (this.sellInput.substring(0, 1) !== '0') { // 0
        this.sellInput = this.sellInput.replace(/[^\d]/g, '') // 
      } else {
        this.sellInput = ''
      }
    }
  },
  beforeCreate () {
    document.querySelector('body').setAttribute('style', 'margin: 0; padding:0;')
  },
  //  
  created () {
    console.log(this.$options.name, 'log in')
    tokenHeadjs.initContract(this.tokens)
  },

  // 
  methods: {
    goTradingCenter () {
      router.push({name: 'token', query: {token: this.$store.getters.getSysTokenSymbol}})
    },
    goApply () {
      router.push({name: 'apply'})
    },
    goSourceCode () {
      // router.push({name: 'SourceCode'})
      // window.location.href = 'https://github.com/freeblockworld/freeblock.wrold/blob/master/seashells'
      window.open('https://etherscan.io/address/0x5cf4C6eE0d1F6F9337AB89A79F1885C748b8beE1#code')
    },
    goWriteBook () {
      // router.push({name: 'WriteBook'})
      window.open('https://github.com/freeblockLuxe/Freeblock.luxe/blob/master/EXChange%20whitebook.pdf')
    }
  },

  //  
  beforeDestroy () {
    console.log(this.$options.name, 'log out')
    document.querySelector('body').removeAttribute('style')
  }
}
</script>

<style scoped>

  .background {
    text-align: center;
    color: white;
    width: 100%;
    height: 100%;
    background: linear-gradient(to right, #9980D6, #4BBF71);
  }
  .floatCenter {
    margin: 0 auto;
    display: inline-block;
  }
  .buttonComm {
    color: white;
    width: 12rem;
    height: 3rem;
    font-family: Helvetica;
    background: transparent;
    border:1px solid white;
    border-radius: 0.3rem;
    box-shadow: 0 0 1rem white;
    cursor: pointer;
  }
  .tokenHead {
    float: left;
    width: 18rem;
    border:1px solid white;
    border-radius: 0.3rem;
    box-shadow: 0 0 1rem white;
  }
  .excessive {
    text-align: center;
    width: 100%;
    height: 100%;
    background: linear-gradient(to right, #9980D6, #4BBF71);
  }

</style>



