import Vue from 'vue'
import Vuex from 'vuex'

// js (vm.$http)
let vm = new Vue()

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    network: '0', //  0 1 
    networkShort: 'Not Login', //  
    tokenHostNetwork: '1', // token 4(rinkeby) 1(main)
    sysTokenSymbol: 'SS', //  token
    website: 'https://freeblock.luxe/#', //  
    mainInfura: 'https://mainnet.infura.io/v3/84101c8a833942018b383e03ca1423f3', //  metamask 
    rinkebyInfura: 'https://rinkeby.infura.io/v3/84101c8a833942018b383e03ca1423f3', //  metamask 
    gasLimit: '300000', // 
    gasPrice: '9000000000', // 9Gwei
    onlyAmbassadors: '', //  
    isAmbassador: '', // 
    ambassadorQuota: '', // 
    ambassadorMaxPurchase: '', // 
    ambassadorAccumulatedQuota: '', // 
    tokenName: 'seashells',
    tokenSymbol: 'SS', // SS
    totalETH: '0',
    totalDividends: '0',
    totalToken: '0',
    myTokens: '0',
    priceInterval: 0.00000001, // token  
    buyPrice: '0',
    buyMinimum: '0.02', //  
    sellPrice: '0',
    sellMinimum: '1', //  
    myDividends: '0',
    myReferredLink: '',
    contractAddress: '',
    customerAddress: 'Not Login Metamask',
    referredBy: '0x0000000000000000000000000000000000000000', // 
    officialReferral: 'https://freeblock.luxe/#/seashells?ss=0xBe1bB799f587249ad8bEf492Cc48E0D5bBB2c991',
    //  /////////////
    tokens: {
      tradQty: 0, //  
      tokenMetadata: {}, // token(...)
      tokenProject: {}, // token(....)
      tokensBaseList: [], // token[{'symbol':'','price':''}] token [tokenBody]
      tokensHeadList: [], // token[{'symbol':'','price':''}] home  token 
      buyPriceList: ['', '', '', '', '', '', '', '', ''], //  9 
      sellPriceList: ['', '', '', '', '', '', '', '', ''], //  9 
      tradingList: [], // 
      myTradingList: [], // 
      noticeList: [], // 
      myTradingForTable: [], // 
      myTradingLengthForTable: 0

    }
  },
  getters: {
    getNetwork (state) {
      if (!state.network) {
        state.network = localStorage.getItem('network')
      }
      return state.network
    },
    getNetworkShort (state) {
      if (!state.networkShort) {
        state.networkShort = localStorage.getItem('networkShort')
      }
      return state.networkShort
    },
    getTokenHostNetwork (state) {
      if (!state.tokenHostNetwork) {
        state.tokenHostNetwork = localStorage.getItem('tokenHostNetwork')
      }
      return state.tokenHostNetwork
    },
    getSysTokenSymbol (state) {
      if (!state.sysTokenSymbol) {
        state.sysTokenSymbol = localStorage.getItem('sysTokenSymbol')
      }
      return state.sysTokenSymbol
    },
    getWebsite (state) {
      if (!state.website) {
        state.website = localStorage.getItem('website')
      }
      return state.website
    },
    getMainInfura (state) {
      if (!state.mainInfura) {
        state.mainInfura = localStorage.getItem('mainInfura')
      }
      return state.mainInfura
    },
    getRinkebyInfura (state) {
      if (!state.rinkebyInfura) {
        state.rinkebyInfura = localStorage.getItem('rinkebyInfura')
      }
      return state.rinkebyInfura
    },
    getTokenName (state) {
      if (!state.tokenName) {
        state.tokenName = localStorage.getItem('tokenName')
      }
      return state.tokenName
    },
    getTokenSymbol (state) {
      return state.tokenSymbol
    },
    getOnlyAmbassadors (state) {
      if (!state.onlyAmbassadors) {
        state.onlyAmbassadors = localStorage.getItem('onlyAmbassadors')
      }
      return state.onlyAmbassadors
    },
    getGasLimit (state) {
      if (!state.gasLimit) {
        state.gasLimit = localStorage.getItem('gasLimit')
      }
      return state.gasLimit
    },
    getGasPrice (state) {
      if (!state.gasPrice) {
        state.gasPrice = localStorage.getItem('gasPrice')
      }
      return state.gasPrice
    },
    getTotalETH (state) {
      if (!state.totalETH) {
        state.totalETH = localStorage.getItem('totalETH')
      }
      return state.totalETH
    },
    getTotalDividends (state) {
      if (!state.totalDividends) {
        state.totalDividends = localStorage.getItem('totalDividends')
      }
      return state.totalDividends
    },
    getTotalToken (state) {
      if (!state.totalToken) {
        state.totalToken = localStorage.getItem('totalToken')
      }
      return state.totalToken
    },
    getBuyPrice (state) {
      if (!state.buyPrice) {
        state.buyPrice = localStorage.getItem('buyPrice')
      }
      return state.buyPrice
    },
    getBuyMinimum (state) {
      if (!state.buyMinimum) {
        state.buyMinimum = localStorage.getItem('buyMinimum')
      }
      return state.buyMinimum
    },
    getSellPrice (state) {
      if (!state.sellPrice) {
        state.sellPrice = localStorage.getItem('sellPrice')
      }
      return state.sellPrice
    },
    getSellMinimum (state) {
      if (!state.sellMinimum) {
        state.sellMinimum = localStorage.getItem('sellMinimum')
      }
      return state.sellMinimum
    },
    getMyTokens (state) {
      if (!state.myTokens) {
        state.myTokens = localStorage.getItem('myTokens')
      }
      return state.myTokens
    },
    getMyDividends (state) {
      if (!state.myDividends) {
        state.myDividends = localStorage.getItem('myDividends')
      }
      return state.myDividends
    },
    getContractAddress (state) {
      if (!state.contractAddress) {
        state.contractAddress = localStorage.getItem('contractAddress')
      }
      return state.contractAddress
    },
    getCustomerAddress (state) {
      if (!state.customerAddress) {
        state.customerAddress = localStorage.getItem('customerAddress')
      }
      return state.customerAddress
    },
    getReferredBy (state) {
      if (!state.referredBy) {
        state.referredBy = localStorage.getItem('referredBy')
      }
      return state.referredBy
    },
    getMyReferredLink (state) {
      return state.myReferredLink
    },
    getOfficialReferral (state) {
      if (!state.officialReferral) {
        state.officialReferral = localStorage.getItem('officialReferral')
      }
      return state.officialReferral
    }
  },
  mutations: {
    setNetwork (state, pm) {
      state.network = pm
    },
    setNetworkShort (state, pm) {
      state.networkShort = pm
    },
    setTokenHostNetwork (state, pm) {
      state.tokenHostNetwork = pm
    },
    setTokenName (state, pm) {
      state.tokenName = pm
    },
    setTokenSymbol (state, pm) {
      state.tokenSymbol = pm
    },
    setOnlyAmbassadors (state, pm) {
      state.onlyAmbassadors = pm
    },
    setIsAmbassador (state, pm) {
      state.isAmbassador = pm
    },
    setAmbassadorMaxPurchase (state, pm) {
      state.ambassadorMaxPurchase = pm
    },
    setAmbassadorQuota (state, pm) {
      state.ambassadorQuota = pm
    },
    setAmbassadorAccumulatedQuota (state, pm) {
      state.ambassadorAccumulatedQuota = pm
    },
    setGasPrice (state, pm) {
      state.gasPrice = pm
    },
    setTotalETH (state, pm) {
      state.totalETH = pm
    },
    setTotalDividends (state, pm) {
      state.totalDividends = pm
    },
    setTotalToken (state, pm) {
      state.totalToken = pm
    },
    setMyTokens (state, pm) {
      state.myTokens = pm
    },
    setBuyPrice (state, pm) {
      state.buyPrice = pm
    },
    setSellPrice (state, pm) {
      state.sellPrice = pm
    },
    setMyDividends (state, pm) {
      state.myDividends = pm
    },
    setContractAddress (state, pm) {
      state.contractAddress = pm
    },
    setCustomerAddress (state, pm) {
      state.customerAddress = pm
    },
    setReferredBy (state, pm) {
      state.referredBy = pm
    },
    setMyReferredLink (state, pm) {
      state.myReferredLink = pm
    },
    setTokenMetadata (state, pm) {
      state.tokens.tokenMetadata = {'name': pm.name, 'symbol': pm.symbol, 'memo': pm.memo, 'tag': pm.tag, 'purchaseLimit': pm.purchaseLimit, 'dividendFee': pm.dividendFee, 'referrerFee': pm.referrerFee, 'ambassadorMaxPurchase': pm.ambassadorMaxPurchase, 'ambassadorQuota': pm.ambassadorQuota, 'onlyAmbassadors': pm.onlyAmbassadors}
    },
    setTokenProject (state, pm) {
      state.tokens.tokenProject = {'sysID': pm.sysID, 'sysTime': pm.sysTime, 'company': pm.company, 'tokenFullName': pm.tokenFullName, 'token': pm.token, 'linkMan': pm.linkMan, 'telephone': pm.telephone, 'otherContacts': pm.otherContacts, 'introduce': pm.introduce, 'status': pm.status, 'tag': pm.tag, 'website': pm.website, 'whiteBook': pm.whiteBook, 'listedTime': pm.listedTime}
    },
    setNoticeList (state, pm) {
      let list = state.tokens.noticeList
      // 30
      if (list.length > 29) {
        list.shift() // 
      } else {
        list.push({'sysID': pm.sysID, 'sysTime': pm.sysTime, 'initiator': pm.initiator, 'title': pm.title, 'content': pm.content}) // 
      }
    },
    setTokenHeadList (state, pm) {
      let tokens = state.tokens.tokensHeadList
      let finded = 0 // tokenListtoken
      if (tokens.length === 0) {
        tokens.unshift({'symbol': pm.symbol, 'price': pm.price})
      } else {
        // tokenlist  
        for (let i = 0; i < tokens.length; i++) {
          // console.log('symbolA', tokens[i].symbol, 'priceA', tokens[i].price, 'pm', pm.symbol, 'Arrlength', tokens.length)
          if (tokens[i].symbol === pm.symbol) {
            tokens[i].price = pm.price
            finded = 1
          }
        }
        if (finded === 0) { // 
          tokens.push({'symbol': pm.symbol, 'price': pm.price})
        }
      }
    },
    setBuyPriceList (state, pm) {
      let list = state.tokens.buyPriceList
      //  
      for (let i = 0; i < list.length; i++) {
        if (i === 0) {
          list[i] = pm
        } else {
          if (pm > parseFloat(state.priceInterval * list.length * 10).toFixed(8)) { // 
            // list[i] = Math.round((parseFloat(list[i - 1]) - parseFloat(state.priceInterval.toString())) * 100000000) / 100000000
            list[i] = Math.round((parseFloat(list[i - 1]) + parseFloat(state.priceInterval.toString())) * 100000000) / 100000000
            // console.log('buylen', list.length, 'price', list[i], 'price+1', list[i - 1])
          }
        }
      }
      list.reverse()
    },
    setSellPriceList (state, pm) {
      let list = state.tokens.sellPriceList
      //  
      for (let i = 0; i < list.length; i++) {
        if (i === 0) {
          list[i] = pm
        } else {
          if (pm > parseFloat(state.priceInterval * list.length * 10).toFixed(8)) { // 
            list[i] = Math.round((parseFloat(list[i - 1]) + parseFloat(state.priceInterval.toString())) * 100000000) / 100000000
            // console.log('selllen', i, list[i], 'price', parseFloat(list[i - 1]) + parseFloat(state.priceInterval.toString()), 'price-1', list[i - 1])
          }
        }
      }
      list.reverse()
    },
    // 
    setTradingList (state, pm) {
      let list = state.tokens.tradingList
      // 30
      if (list.length > 29) {
        // list.pop() // 
        list.shift() // 
      }
      list.unshift({'sysID': pm.sysID, 'action': pm.action, 'price': pm.price, 'amount': pm.amount, 'qty': pm.qty}) // 
    },
    // 
    setMyTradingForTable (state, pm) {
      let listForTable = state.tokens.myTradingForTable
      listForTable.unshift({'sysID': pm.sysID, 'token': pm.token, 'time': pm.time, 'account': pm.account, 'type': pm.type, 'action': pm.action, 'price': pm.price, 'amount': pm.amount, 'qty': pm.qty, 'myTokens': pm.myTokens, 'myDividends': pm.myDividends, 'status': pm.status}) // 
    },
    // 
    setMyTradingList (state, pm) {
      // console.log('EXTrade post tokenSymbol', pm.token, 'qty', pm.qty, 'Address', pm.account, 'buyPrice', pm.price)
      let list = state.tokens.myTradingList
      list.unshift({'sysID': pm.sysID, 'token': pm.token, 'time': pm.time, 'account': pm.account, 'type': pm.type, 'action': pm.action, 'price': pm.price, 'amount': pm.amount, 'qty': pm.qty, 'myTokens': pm.myTokens, 'myDividends': pm.myDividends, 'status': pm.status}) // 
      state.tokens.myTradingLengthForTable = state.tokens.myTradingList.length
      //  TradingRecordload
      if (pm.type === 'TradingRecord') {
        vm.$http.post('/api/EXTrade',
          {
            token: pm.token,
            account: pm.account,
            time: pm.time,
            action: pm.action,
            price: pm.price,
            amount: pm.amount,
            qty: pm.qty,
            myTokens: pm.myTokens,
            myDividends: pm.myDividends,
            status: pm.status,
            hash: pm.hash,
            emulateJSON: true
          })
          .then((data) => {
            console.log('EXTrade post to database')
            // 
            // let list = state.tokens.myTradingForTable
            // list.push({'sysID': pm.sysID, 'token': state.tokenSymbol, 'time': pm.time, 'account': state.customerAddress, 'type': pm.type, 'action': pm.action, 'price': state.buyPrice, 'amount': pm.amount, 'qty': state.tokens.tradQty, 'status': pm.status}) // 
          }, (error) => {
            console.log('EXTrade post error', error)
          })
      }
    }
  }
})

export default store



