<template>
  <div class="background">
    <!--title-->
    <div class="excessive">
      <my-title/>
    </div>

    <div class="floatCenter">
    <div style="margin-top: 5rem">
      <div class="input_box">
        <div style="font-size: x-large"></div>
      </div>
      <div class="input_box" style="margin-top: 2rem">
        <div> <span style="color: orangered;">*</span></div>
        <input class="input_text" type="text" maxlength="30" v-model="company"/>
      </div>
      <div class="input_box">
        <div> <span style="color: orangered;">*</span></div>
        <input class="input_text" type="text" maxlength="30" v-model="tokenFullName"/>
      </div>
      <div class="input_box">
        <div> <span style="color: orangered;">*</span><span class="inputTitleHint"> 5</span></div>
        <input class="input_text" type="text" maxlength="10" v-model="token"/>
      </div>
      <div class="input_box">
        <div> <span style="color: orangered;">*</span></div>
        <input class="input_text" type="text" maxlength="30" v-model="linkMan"/>
      </div>
      <div class="input_box">
        <div> <span style="color: orangered;">*</span></div>
        <input class="input_text" type="text" maxlength="30" v-model="telephone"/>
      </div>
      <div class="input_box">
        <div></div>
        <input class="input_text" type="text" maxlength="30" v-model="otherContacts"/>
      </div>
      <div class="input_box">
        <div></div>
        <input class="input_text" type="text" maxlength="150" placeholder='' style="width: 52rem" v-model="website"/>
      </div>
      <div class="input_box">
        <div></div>
        <input class="input_text" type="text" maxlength="150" placeholder='' style="width: 52rem" v-model="whiteBook"/>
      </div>
      <div class="input_box">
        <div>
          <span style="color: orangered;">*</span>
          <span class="inputTitleHint">300 3000 </span>
          <span class="inputTitleHint">{{this.textLength}} / 3000</span>
        </div>
        <textarea class="input_text" maxlength="3000" @input="inputLength" style="height: 19rem; width: 52rem" type="text" v-model="introduce"/>
      </div>
      <div class="input_box" style="margin-top: 2rem">
        <button class="block_button" @click="apply"></button>
      </div>
    </div>
    </div>

      <!--boot-->
      <div class="excessive" style="margin-top: 2rem; padding-top: 3rem;padding-bottom: 2rem">
        <myFoot/>
      </div>

  </div>
</template>

<script>
import myTitle from '../components/TitleEx'
import myFoot from '../components/FootEx'

export default {
  name: 'apply',
  components: {myTitle, myFoot},
  data: function () {
    return {
      company: '',
      tokenFullName: '',
      token: '',
      linkMan: '',
      telephone: '',
      otherContacts: '',
      introduce: '',
      status: '',
      website: '',
      whiteBook: '',
      textLength: 0
    }
  },
  beforeCreate () {
    document.querySelector('body').setAttribute('style', 'margin: 0; padding:0;')
  },
  // 
  methods: {
    inputLength () {
      this.textLength = this.introduce.length
    },
    apply () {
      var error_ = ''
      if (this.company.length < 2) {
        error_ += '2'
      }
      if (this.tokenFullName.length < 3) {
        error_ += '\n3'
      }
      if (this.token.length < 2) {
        error_ += '\n2'
      }
      if (this.linkMan.length < 2) {
        error_ += '\n2'
      }
      if (this.telephone.length < 5) {
        error_ += '\n5'
      }

      if (this.introduce.length < 300) {
        error_ += '\n300'
      }
      //  varchar  (0x80131904): 'post' 
      if (this.introduce.indexOf("'") > 0) {
        error_ += '\n '
      }

      if (error_.length < 1) {
        this.$http.get('/api/token/findToken?token=' + this.token, {emulateJSON: true})
          .then((response) => {
            // console.log('findToken', this.token, response.data)
            // token  
            if (response.data !== '') {
              if (response.data === '') {
                if (confirm('' + this.token + ' ?')) {
                  // this.updateToken()
                  // put  
                  this.addToken()
                }
              } else {
                alert('' + this.token + '')
              }
            } else {
              this.addToken()
            }
          })
      } else {
        alert(error_)
      }
    },

    addToken () {
      this.$http.post('/api/token',
        {
          company: this.company,
          tokenFullName: this.tokenFullName,
          token: this.token,
          linkMan: this.linkMan,
          telephone: this.telephone,
          otherContacts: this.otherContacts,
          introduce: this.introduce.toString(),
          status: this.status,
          website: this.website,
          whiteBook: this.whiteBook,
          emulateJSON: true
        })
        .then((response) => {
          console.log('post', response.data)
          if (parseInt(response.data) > 0) {
            alert(' ')
          } else {
            alert('')
          }
        }, (error) => {
          alert(error + '\n')
        })
    },

    // put  
    updateToken () {
      this.$http.put('/api/token',
        {
          company: this.company,
          tokenFullName: this.tokenFullName,
          token: this.token,
          linkMan: this.linkMan,
          telephone: this.telephone,
          otherContacts: this.otherContacts,
          introduce: this.introduce,
          status: this.status,
          website: this.website,
          whiteBook: this.whiteBook,
          emulateJSON: true })
        .then((response) => {
          console.log('put', response.data)
          if (parseInt(response.data) > 0) {
            alert('')
          } else {
            alert('')
          }
        }, (error) => {
          alert(error + '\n')
        })
    }
  },
  //  
  beforeDestroy () {
    console.log(this.$options.name, 'log out')
    document.querySelector('body').removeAttribute('style')
  }
}
</script>

<style scoped>
  .floatCenter {
    margin: 0 auto;
    display: inline-block;
  }
  .background {
    text-align: center;
    margin: 0;
    padding: 0;
    border: 0;
    width: 100%;
    height: 100%;
    background-color: #E5EBF5;
  }
  .input_box {
    text-align:left;
    margin: 2rem
  }
  .inputTitleHint {
    margin-left: 1rem;
    color: green;
    font-size:small;
  }
  .input_text {
    margin: 0;
    padding: 0;
    height: 3rem;
    width: 29rem;
    font-size: 1rem;
    border: 1px solid #D8D8D8;
    border-image: none;
    text-indent: 0.2rem;
  }
  .block_button {
    width: 12rem;
    height: 2.5rem;
    font-size: large;
    background-color: #9980D6;
    color: rgba(250, 250, 250, 1);
    margin: 1rem auto 0;
    cursor: pointer;
  }
  .excessive {
    text-align: center;
    width: 100%;
    height: 100%;
    background: linear-gradient(to right, #9980D6, #4BBF71);
  }

</style>



