function getStaticInfo () {
  tokenContract.symbol()
    .then((result) => {
      let rest = result[0].toString()
      console.log('symbol: ' + rest)
      Store.commit('setTokenSymbol', rest)
      getTradingFromWebApi(rest)
    })

  tokenContract.name()
    .then((result) => {
      let rest = result[0].toString()
      Store.commit('setTokenName', rest)
    })

  if (Store.getters.getTokenSymbol === Store.getters.getSysTokenSymbol) {
    //  seashells getMetadata; 
    let pm = {
      'name': Store.getters.getTokenName,
      'symbol': Store.getters.getTokenSymbol,
      'memo': 'seashells',
      'tag': '1000000000',
      'purchaseLimit': '5000000',
      'dividendFee': 10,
      'referrerFee': 3,
      'ambassadorMaxPurchase': 1,
      'ambassadorQuota': 20,
      'onlyAmbassadors': false
    }
    Store.commit('setTokenMetadata', pm)
  } else {
    tokenContract.onlyAmbassadors()
      .then((result1) => {
        let rest1 = result1[0].toString()
        Store.commit('setOnlyAmbassadors', rest1)
        //  
        if (rest1 === 'true') {
          // 
          tokenContract.ambassadorMaxPurchase_()
            .then((result2) => {
              let rest2 = web3js.fromWei(result2[0].toString())
              Store.commit('setAmbassadorMaxPurchase', rest2)
              console.log('OnlyAmbassadors', rest1, '\n' + Store.getters.getCustomerAddress, '\nIsAmbassador', Store.state.isAmbassador)
            })
          // 
          tokenContract.ambassadorQuota_()
            .then((result3) => {
              let rest3 = web3js.fromWei(result3[0].toString())
              Store.commit('setAmbassadorQuota', rest3)
              // console.log('setIsAmbassador', Store.state.isAmbassador, Store.getters.getCustomerAddress)
            })
        }
      })

    tokenContract.getMetadata()
      .then((result) => {
        let name = result[0].toString()
        let symbol = result[1].toString()
        let memo = result[2].toString()
        let tag = result[3].toString()
        let purchaseLimit = web3js.fromWei(result[4].toString())
        let dividendFee = result[5].toString()
        let referrerFee = result[6].toString()
        let ambassadorMaxPurchase = web3js.fromWei(result[7].toString())
        let ambassadorQuota = web3js.fromWei(result[8].toString())
        let onlyAmbassadors = result[9].toString()
        let pm = {
          'name': name,
          'symbol': symbol,
          'memo': memo,
          'tag': tag,
          'purchaseLimit': purchaseLimit,
          'dividendFee': dividendFee,
          'referrerFee': referrerFee,
          'ambassadorMaxPurchase': ambassadorMaxPurchase,
          'ambassadorQuota': ambassadorQuota,
          'onlyAmbassadors': onlyAmbassadors
        }
        Store.commit('setTokenMetadata', pm)
      })
  }

  // 
  // getShareDividendRecord()
}

// 
function getDynamicInfo () {
  totalSupply()
  totalEthereumBalance()
  buyPrice()
  sellPrice()

  // 
  if (typeof window.web3 !== 'undefined') {
    // 

    if (window.web3.eth.coinbase !== null && typeof window.web3.eth.coinbase !== 'undefined' && Store.getters.getNetwork === Store.getters.getTokenHostNetwork) {
      let myAddress = window.web3.eth.coinbase
      let isChangeAccount = false
      if (Store.getters.getCustomerAddress !== myAddress) {
        // 
        console.log('account change', myAddress)
        isChangeAccount = true
        accountClear()
        getMyTradingFromWebApi(myAddress)
      }
      // console.log('setCustomerAddress1', window.web3.eth.coinbase, Store.getters.getCustomerAddress, Store.getters.getNetwork, Store.getters.getPresetNetwork)
      Store.commit('setCustomerAddress', myAddress)
      //  
      if (Store.getters.getOnlyAmbassadors === 'true') {
        // 
        tokenContract.ambassadorAccumulatedQuota_(myAddress)
          .then((result2) => {
            let rest = web3js.fromWei(result2[0].toString())
            Store.commit('setAmbassadorAccumulatedQuota', rest)
          })

        // 
        tokenContract.ambassadors_(myAddress)
          .then((result3) => {
            let rest = result3[0].toString()
            Store.commit('setIsAmbassador', rest)
          })
      }

      // 
      myTokens(myAddress, false)
      myDividends(1, myAddress, false, isChangeAccount)
    }
  }
  // 12,setInterval
  setTimeout(function () {
    if (tokenContract !== null) {
      getDynamicInfo()
    }
  }, 1000 * 12)
}

