// token isEvent 0
function myTokens (fromAddress) {
  tokenContract.myTokens({from: fromAddress})
    .then((result) => {
      // console.log('myTokens', result[0].toString())
      if (result[0].toString() > 0) {
        let rest = Math.round(web3js.fromWei(result[0].toString()) * 100000000) / 100000000
        if (rest > 100) {
          // 
          Store.commit('setMyReferredLink', Store.getters.getWebsite + '/token/?token=' + Store.getters.getTokenSymbol + '&by=' + fromAddress)
        } else {
          Store.commit('setMyReferredLink', '')
        }
        Store.commit('setMyTokens', rest)
      }
    })
}

// [referre] 1:0, isEven 0
function myDividends (bool, fromAddress, isEvent, isChangeAccout) {
  tokenContract.myDividends(bool, {from: fromAddress})
    .then((result) => {
      let rest = Math.round(web3js.fromWei(result[0].toString()) * 100000000) / 100000000
      let restFloat = parseFloat(rest)
      let myDividendsFloat = parseFloat(Store.getters.getMyDividends)
      // console.log('myDividends', rest, myDividendsFloat, Store.getters.getMyDividends, '\naddr', isChangeAccout)
      if (isChangeAccout === false && restFloat !== myDividendsFloat) {
        // 
        let amount_ = ''
        let status_ = ''
        if (restFloat > myDividendsFloat) {
          amount_ = restFloat - myDividendsFloat
          status_ = '+'
        } else {
          amount_ = myDividendsFloat - restFloat
          status_ = '-'
        }
        let dateTime_ = Global.getCurrentTime()
        let record = {
          'token': Store.getters.getTokenSymbol,
          'time': dateTime_,
          'account': fromAddress,
          'type': 'TradingRecord',
          'action': '',
          'price': '',
          'amount': amount_,
          'qty': '',
          'myTokens': '',
          'myDividends': rest,
          'status': status_,
          'hash': ''
        }
        Store.commit('setMyTradingList', record) // 
        Store.commit('setMyTradingForTable', record) // 
      }

      // 
      if (rest > 0) {
        Store.commit('setMyDividends', rest)
      } else { // 
        if (isEvent) {
          Store.commit('setMyDividends', '0')
        }
      }
    })
}

function eventUpdate (bool, fromAddress, pmArray) {
  tokenContract.myDividends(bool, {from: fromAddress})
    .then((result) => {
      console.log('res: ' + result[0].toString() + ' isevent ' + pmArray)

      if (result[0].toString() > 0) {
        let rest = Math.round(web3js.fromWei(result[0].toString()) * 100000000) / 100000000
        Store.commit('setMyDividends', rest)
      } else { // 
        Store.commit('setMyDividends', '0')
      }

      tokenContract.myTokens({from: fromAddress})
        .then((result) => {
          if (result[0].toString() > 0) {
            let rest = Math.round(web3js.fromWei(result[0].toString()) * 100000000) / 100000000
            if (rest > 100) {
              // 
              // Store.commit('setMyReferredLink', Store.getters.getWebsite + '/?ss=' + fromAddress)
              Store.commit('setMyReferredLink', Store.getters.getWebsite + '/token/?token=' + Store.getters.getTokenSymbol + '&by=' + fromAddress)
            } else {
              Store.commit('setMyReferredLink', '')
            }
            Store.commit('setMyTokens', rest)
          }

          let record = {'token': pmArray.token, 'time': pmArray.time, 'account': pmArray.account, 'type': pmArray.type, 'action': pmArray.action, 'price': pmArray.price, 'amount': pmArray.amount, 'qty': pmArray.qty, 'myTokens': Store.getters.getMyTokens, 'myDividends': Store.getters.getMyDividends, 'status': 'CONFIRM'}
          Store.commit('setMyTradingList', record)
          Store.commit('setMyTradingForTable', record) // 
        })
    })
}

