function accountClear () {
  // console.log('Not account login')
  Store.commit('setCustomerAddress', 'Not Login Metamask')
  Store.commit('setMyReferredLink', '')
  Store.commit('setMyTokens', '0')
  Store.commit('setMyDividends', '0')
  Store.commit('setBuyPrice', '0')
  Store.commit('setSellPrice', '0')
  // 
  if (Store.state.tokens.myTradingForTable.length > 0) {
    Store.state.tokens.myTradingForTable.splice(0, Store.state.tokens.myTradingForTable.length)
  }
  if (Store.state.tokens.myTradingList.length > 0) {
    Store.state.tokens.myTradingList.splice(0, Store.state.tokens.myTradingList.length)
  }

  Store.state.tokens.myTradingLengthForTable = 0
  console.log('accountClear', Store.state.tokens.myTradingForTable.length, Store.state.tokens.myTradingList.length, Store.state.tokens.myTradingLengthForTable)
}

//  
function tokenClear () {
  // 
  if (Store.state.tokens.tradingList.length > 0) {
    Store.state.tokens.tradingList.splice(0, Store.state.tokens.tradingList.length)
  }

  let bpl = Store.state.tokens.buyPriceList
  for (let i = 0; i < bpl.length; i++) {
    bpl[i] = ''
  }
  let spl = Store.state.tokens.sellPriceList
  for (let i = 0; i < spl.length; i++) {
    spl[i] = ''
  }
}

