async function buy (buyAmount) {
  console.log('************* buy **************')

  if (checkExpress() === 0) {
    return
  }
  // metamask
  if (typeof window.web3 === 'undefined') {
    if (confirm('metamask' + '\nmetamask')) {
      window.open('https://metamask.io')
    }
    return
  }

  await window.web3.eth.getCoinbase((er, myAddress) => {
    if (myAddress == null) {
      alert('metamask')
    } else {
      var buyAmountWei = window.web3.toWei(buyAmount)

      // token
      if (Store.getters.getNetwork !== Store.getters.getTokenHostNetwork) {
        alert('') //  
        return
      }

      if (buyAmount < Store.getters.getBuyMinimum) {
        alert(' 0.02 eth')
        return
      }
      // 
      if (!window.web3.isAddress(Store.getters.getContractAddress)) {
        alert('')
        return
      }

      // 
      if (Store.getters.getTokenSymbol !== Store.getters.getSysTokenSymbol && parseFloat(Store.getters.getTotalToken) > parseFloat(Store.state.tokens.tokenMetadata.purchaseLimit)) {
        alert(' ' + Store.state.tokens.tokenMetadata.purchaseLimit)
        return
      }

      //  
      if (Store.getters.getOnlyAmbassadors) {
        // 
        if (!Store.state.isAmbassador) {
          alert('')
          return
        }

        if ((parseFloat(Store.getters.getTotalETH) + parseFloat(buyAmount)) > parseFloat(Store.state.ambassadorQuota)) {
          alert('')
          return
        }

        if ((parseFloat(Store.state.ambassadorAccumulatedQuota) + parseFloat(buyAmount)) > parseFloat(Store.state.ambassadorMaxPurchase)) {
          let tmp = parseFloat(Store.state.ambassadorMaxPurchase) - parseFloat(Store.state.ambassadorAccumulatedQuota)
          alert(' ' + Store.state.ambassadorMaxPurchase + ' ETH' + '\n ' + Store.state.ambassadorAccumulatedQuota + '\n ' + tmp)
          return
        }
      }

      console.log('\nfrom: ' + myAddress + '\nto: ' + Store.getters.getContractAddress + '\nvalue: ' + web3js.fromWei(buyAmountWei) + '\nreferredBy: ' + Store.getters.getReferredBy)

      // 
      var watchOnTokenPurchase = tokenContract.onTokenPurchase()
      watchOnTokenPurchase.new({
        'fromBlock': 'latest',
        'toBlock': 'latest',
        'address': Store.getters.getContractAddress
      }).then((filterId) => {
        // console.log('event buy Filter id ' + filterId + ' created')
      })
      watchOnTokenPurchase.watch((error, result) => {
        if (!error) {
          console.log('buy event', result)
          var res0 = result.args.customerAddress
          var res1 = web3js.fromWei(result.args.incomingEthereum)
          var res2 = web3js.fromWei(result.args.tokensMinted)
          var res3 = result.args.referredBy
          console.log('Purchase result \ncustomerAddress: ' + res0 + ' \nincomingEthereum: ' + res1 + ' \ntokensMinted: ' + res2 + ' \nreferredBy: ' + res3)
          Store.commit('setMyTokens', res2.toString())
          Store.commit('setTotalETH', totalEthereumBalance())
        } else {
          console.log('buy event else')
          let dateTime_ = Global.getCurrentTime()
          let record = {
            'token': Store.getters.getTokenSymbol,
            'time': dateTime_,
            'account': myAddress,
            'type': 'TradingRecord',
            'action': '',
            'price': Store.getters.getBuyPrice,
            'amount': buyAmount,
            'qty': Store.state.tokens.tradQty,
            'status': 'CONFIRM'
          }
          eventUpdate(1, myAddress, record)
        }
        watchOnTokenPurchase.uninstall()
      })

      //  
      tokenContract.calculateTokensReceived(buyAmountWei)
        .then((result) => {
          if (result[0].toString() > 0) {
            let rest = web3js.fromWei(result[0].toString())
            Store.state.tokens.tradQty = rest
          }
        })

      // 
      // seashells.buy(referredBy, { from: myAddress, to: Store.getters.getContractAddress, value: buyToken, gas: Store.getters.getGasLimit, gasPrice: Store.getters.getGasPrice },
      tokenContract.buy(Store.getters.getReferredBy, {from: myAddress, value: buyAmountWei, gasPrice: Store.getters.getGasPrice * 2},
        function (error, result) {
          if (!error) {
            let dateTime_ = Global.getCurrentTime()
            let record = {
              'token': Store.getters.getTokenSymbol,
              'time': dateTime_,
              'account': myAddress,
              'type': 'TradingRecord',
              'action': '',
              'price': Store.getters.getBuyPrice,
              'amount': buyAmount,
              'qty': Store.state.tokens.tradQty,
              'myTokens': Store.getters.getMyTokens,
              'myDividends': Store.getters.getMyDividends,
              'status': 'PENDING',
              'hash': result
            }
            Store.commit('setMyTradingList', record)
            Store.commit('setMyTradingForTable', record) // 

            // let localStorageKey = 'TradingRecord' + dateTime_
            // localStorage.setItem(localStorageKey, JSON.stringify(record)) // localStorag
          } else {
            let dateTime_ = Global.getCurrentTime()
            let record = {
              'token': Store.getters.getTokenSymbol,
              'time': dateTime_,
              'account': myAddress,
              'type': 'TradingRecord',
              'action': '',
              'price': Store.getters.getBuyPrice,
              'amount': buyAmount,
              'qty': Store.state.tokens.tradQty,
              'myTokens': Store.getters.getMyTokens,
              'myDividends': Store.getters.getMyDividends,
              'status': 'REJECT',
              'hash': result
            }
            Store.commit('setMyTradingList', record) // 
            Store.commit('setMyTradingForTable', record) // 

            // let localStorageKey = 'TradingRecord' + dateTime_
            // localStorage.setItem(localStorageKey, JSON.stringify(record)) // localStorag
          }
        })
    }
  })
}

async function sell (_sellTokens) {
  console.log('************* sell **************')
  // metamask
  if (typeof window.web3 === 'undefined') {
    if (confirm('metamask' + '\nmetamask')) {
      window.open('https://metamask.io')
    }
    return
  }

  // 
  await window.web3.eth.getCoinbase((er, myAddress) => {
    if (myAddress == null) {
      alert('metamask')
    } else {
      // 
      if (Store.getters.getNetwork !== Store.getters.getTokenHostNetwork) {
        alert('METAMASK') //  
        return
      }
      // 
      if (_sellTokens < Store.getters.getSellMinimum) {
        alert(' 1 ' + Store.getters.getTokenSymbol)
        return
      }

      // 
      if (!window.web3.isAddress(Store.getters.getContractAddress)) {
        alert('')
        return
      }

      let sellTokenToWei = window.web3.toWei(_sellTokens)
      // 
      tokenContract.myTokens({from: myAddress})
        .then((result) => {
          let mybalance = parseInt(window.web3.fromWei(result[0].toString()))
          console.log('sell  address: ' + myAddress + ' \nbalance: ' + mybalance + ' \ntokenOfSell: ' + _sellTokens)
          // 
          if (mybalance > _sellTokens) {
            // 
            var watchOnTokenSell = tokenContract.onTokenSell()
            watchOnTokenSell.new()
            watchOnTokenSell.watch(function (error, result) {
              if (!error) {
                console.log('sell event', result)
                var res0 = result.args.customerAddress
                var res1 = window.web3.fromWei(result.args.tokensBurned)
                var res2 = window.web3.fromWei(result.args.ethereumEarned)
                console.log('sell result \ncustomerAddress: ' + res0 + ' \ntokensBurned: ' + res1 + ' \nethereumEarned: ' + res2)
              } else {
                console.log('sell event else')

                let dateTime_ = Global.getCurrentTime()
                let record = {'token': Store.getters.getTokenSymbol, 'time': dateTime_, 'account': myAddress, 'type': 'TradingRecord', 'action': '', 'price': Store.getters.getBuyPrice, 'amount': Store.state.tokens.tradQty, 'qty': _sellTokens, 'status': 'CONFIRM'}
                eventUpdate(1, myAddress, record)
              }
              watchOnTokenSell.uninstall()
            })

            //  
            tokenContract.calculateEthereumReceived(sellTokenToWei)
              .then((result) => {
                if (result[0].toString() > 0) {
                  let rest = result[0].toString()
                  Store.state.tokens.tradQty = window.web3.fromWei(rest)
                  // 
                  tokenContract.sell(sellTokenToWei, { from: myAddress, gasPrice: Store.getters.getGasPrice * 2 },
                    function (error, result) {
                      if (!error) {
                        let dateTime_ = Global.getCurrentTime()
                        let record = {'token': Store.getters.getTokenSymbol, 'time': dateTime_, 'account': myAddress, 'type': 'TradingRecord', 'action': '', 'price': Store.getters.getSellPrice, 'amount': Store.state.tokens.tradQty, 'qty': _sellTokens, 'myTokens': Store.getters.getMyTokens, 'myDividends': Store.getters.getMyDividends, 'status': 'PENDING', 'hash': result}
                        Store.commit('setMyTradingList', record)
                        Store.commit('setMyTradingForTable', record) // 
                      } else {
                        let dateTime_ = Global.getCurrentTime()
                        let record = {'token': Store.getters.getTokenSymbol, 'time': dateTime_, 'account': myAddress, 'type': 'TradingRecord', 'action': '', 'price': Store.getters.getSellPrice, 'amount': Store.state.tokens.tradQty, 'qty': _sellTokens, 'myTokens': Store.getters.getMyTokens, 'myDividends': Store.getters.getMyDividends, 'status': 'REJECT', 'hash': result}
                        Store.commit('setMyTradingList', record) // 
                        Store.commit('setMyTradingForTable', record) // 
                      }
                    })
                }
              })
          } else {
            alert('balance: ' + mybalance + '\nsell: ' + _sellTokens + '\nsell amount out of you balance')
          }
        })
    }
  })
}

