async function reinvest () {
  console.log('************* reinvest **************')
  // metamask
  if (typeof window.web3 === 'undefined') {
    if (confirm('metamask' + '\nmetamask')) {
      window.open('https://metamask.io')
    }
    return
  }

  await window.web3.eth.getCoinbase((er, myAddress) => {
    if (myAddress == null) {
      alert('metamask')
    } else {
      // 
      if (Store.getters.getNetwork !== Store.getters.getTokenHostNetwork) {
        alert('METAMASK') //  
        return
      }
      // 
      if (!window.web3.isAddress(Store.getters.getContractAddress)) {
        alert('')
        return
      }

      // 
      if (parseFloat(Store.getters.getTotalToken) > parseFloat(Store.state.tokens.tokenMetadata.purchaseLimit)) {
        alert(' ' + Store.state.tokens.tokenMetadata.purchaseLimit)
        return
      }

      tokenContract.myDividends(1, {from: myAddress})
        .then((result) => {
          let myDividends_ = window.web3.fromWei(result[0].toString())
          if (myDividends_ < Store.getters.getBuyMinimum) {
            alert(' 0.02 eth')
          } else {
            //  
            if (Store.getters.getOnlyAmbassadors) {
              // 
              if (!Store.state.isAmbassador) {
                alert('')
                return
              }

              if ((parseFloat(Store.getters.getTotalETH) + parseFloat(myDividends_)) > parseFloat(Store.state.ambassadorQuota)) {
                alert('')
                return
              }

              if ((parseFloat(Store.state.ambassadorAccumulatedQuota) + parseFloat(myDividends_)) > parseFloat(Store.state.ambassadorMaxPurchase)) {
                let tmp = parseFloat(Store.state.ambassadorMaxPurchase) - parseFloat(Store.state.ambassadorAccumulatedQuota)
                alert(' ' + Store.state.ambassadorMaxPurchase + ' ETH' + '\n ' + Store.state.ambassadorAccumulatedQuota + '\n ' + tmp)
                return
              }
            }

            // 
            var watchOnReinvestment = tokenContract.onReinvestment()
            watchOnReinvestment.new()
            watchOnReinvestment.watch(function (error, result) {
              if (!error) {
                var res0 = result.args.customerAddress
                var res1 = result.args.ethereumReinvested
                var res2 = result.args.tokensMinted
                console.log('Reinvestment result \ncustomerAddress: ' + res0 + ' \nethereumReinvested: ' + res1 + ' \ntokensMinted: ' + res2)
              } else {
                console.log('reinvest event else')
                let dateTime_ = Global.getCurrentTime()
                let record = {'token': Store.getters.getTokenSymbol, 'time': dateTime_, 'account': myAddress, 'type': 'TradingRecord', 'action': '', 'price': Store.getters.getBuyPrice, 'amount': myDividends_, 'qty': Store.state.tokens.tradQty, 'status': 'CONFIRM'}
                eventUpdate(1, myAddress, record)
              }
              watchOnReinvestment.uninstall()
            })

            //  
            tokenContract.calculateTokensReceived(window.web3.toWei(myDividends_))
              .then((result) => {
                if (result[0].toString() > 0) {
                  let rest = result[0].toString()
                  Store.state.tokens.tradQty = window.web3.fromWei(rest)

                  console.log('reinvest', myDividends_, window.web3.toWei(myDividends_), Store.state.tokens.tradQty)
                  // 
                  tokenContract.reinvest({ from: myAddress, gasPrice: Store.getters.getGasPrice * 2 },
                    function (error, result) {
                      if (!error) {
                        let dateTime_ = Global.getCurrentTime()
                        let record = {'token': Store.getters.getTokenSymbol, 'time': dateTime_, 'account': myAddress, 'type': 'TradingRecord', 'action': '', 'price': Store.getters.getBuyPrice, 'amount': myDividends_, 'qty': Store.state.tokens.tradQty, 'myTokens': Store.getters.getMyTokens, 'myDividends': Store.getters.getMyDividends, 'status': 'PENDING', 'hash': result}
                        Store.commit('setMyTradingList', record)
                        Store.commit('setMyTradingForTable', record) // 

                        // let localStorageKey = 'TradingRecord' + dateTime_
                        // localStorage.setItem(localStorageKey, JSON.stringify(record)) // localStorag
                      } else {
                        let dateTime_ = Global.getCurrentTime()
                        let record = {'token': Store.getters.getTokenSymbol, 'time': dateTime_, 'account': myAddress, 'type': 'TradingRecord', 'action': '', 'price': Store.getters.getBuyPrice, 'amount': myDividends_, 'qty': Store.state.tokens.tradQty, 'myTokens': Store.getters.getMyTokens, 'myDividends': Store.getters.getMyDividends, 'status': 'REJECT', 'hash': result}
                        Store.commit('setMyTradingList', record) // 
                        Store.commit('setMyTradingForTable', record) // 
                      }
                    })
                }
              })
          }
        })
    }
  })
}

