function buyPrice () {
  tokenContract.buyPrice()
    .then((result) => {
      // console.log('buyPrice', result[0].toString(), Store.getters.getBuyPrice)
      if (result[0].toString() > 0) {
        let rest = web3js.fromWei(result[0].toString())
        if (rest !== Store.getters.getBuyPrice) {
          Store.commit('setBuyPrice', rest)
          Store.commit('setBuyPriceList', rest)
        }
      }
    })
}

function sellPrice () {
  tokenContract.sellPrice()
    .then((result) => {
      // console.log('sellPrice', result[0].toString(), Store.getters.getSellPrice)
      if (result[0].toString() > 0) {
        let rest = web3js.fromWei(result[0].toString())
        if (rest !== Store.getters.getSellPrice) {
          Store.commit('setSellPrice', rest)
          Store.commit('setSellPriceList', rest)
        }
      }
    })
}

