async function shareDividend (amount) {
  console.log('************* shareDividend **************')
  // metamask
  if (typeof window.web3 === 'undefined') {
    if (confirm('you need to install Metamask' + '\nlink to Metamask website now')) {
      window.open('https://metamask.io')
    }
    return
  }

  // 
  if (Store.getters.getNetwork !== Store.getters.getTokenHostNetwork) {
    alert('METAMASK') //  
    return
  }

  // 
  if (amount < 0.02) {
    alert('greater than or equal to 1 eth')
    return
  }

  await window.web3.eth.getCoinbase((er, myAddress) => {
    if (myAddress == null) {
      alert('You need login Metamask')
    } else {
      var mateMastValue = window.web3.toWei(amount)
      // 
      var watchOnShareDividend = tokenContract.onShareDividend()
      watchOnShareDividend.new({
        'fromBlock': 'latest',
        'toBlock': 'latest',
        'address': Store.getters.getContractAddress
      }).then((filterId) => {
        console.log('\nFilter id ' + filterId + ' created')
      })
      watchOnShareDividend.watch((error, result) => {
        if (!error) {
          console.log('Dividend event', result)
          var res0 = result.args.customerAddress
          var res1 = result.args.time
          var res2 = window.web3.fromWei(result.args.amount)
          var res3 = window.web3.fromWei(result.args.currentProfitPerShare)
          var res4 = window.web3.fromWei(result.args.profitPerShare)
          console.log('Dividend result \ncustomerAddress: ' + res0 + ' \ntime: ' + res1 + ' \namount: ' + res2 + ' \ncurrentProfitPerShare: ' + res3 + ' \nprofitPerShare: ' + res4)
        } else {
          console.log('Dividend event else')
          let dateTime_ = Global.getCurrentTime()
          let record = {'token': Store.getters.getTokenSymbol, 'time': dateTime_, 'account': myAddress, 'type': 'TradingRecord', 'action': '', 'price': Store.getters.getBuyPrice, 'amount': amount, 'qty': '', 'status': 'CONFIRM'}
          eventUpdate(1, myAddress, record)
        }
        watchOnShareDividend.uninstall()
      })

      // 
      tokenContract.shareDivies({from: myAddress, value: mateMastValue, gasPrice: Store.getters.getGasPrice * 2},
        function (error, result) {
          if (!error) {
            let dateTime_ = Global.getCurrentTime()
            let record = {'token': Store.getters.getTokenSymbol, 'time': dateTime_, 'account': myAddress, 'type': 'TradingRecord', 'action': '', 'price': Store.getters.getBuyPrice, 'amount': amount, 'qty': '', 'myTokens': Store.getters.getMyTokens, 'myDividends': Store.getters.getMyDividends, 'status': 'PENDING', 'hash': result}
            Store.commit('setMyTradingList', record)
            Store.commit('setMyTradingForTable', record) // 
          } else {
            let dateTime_ = Global.getCurrentTime()
            let record = {'token': Store.getters.getTokenSymbol, 'time': dateTime_, 'account': myAddress, 'type': 'TradingRecord', 'action': '', 'price': Store.getters.getBuyPrice, 'amount': amount, 'qty': '', 'myTokens': Store.getters.getMyTokens, 'myDividends': Store.getters.getMyDividends, 'status': 'REJECT', 'hash': result}
            Store.commit('setMyTradingList', record)
            Store.commit('setMyTradingForTable', record) // 
          }
        })
    }
  })
}
// 
function calculateTokensReceived (_pm) {
  if (_pm < Store.getters.getBuyMinimum) {
    alert('greater than or equal to 0.02 eth')
    return
  }
  let pm_ = web3js.toWei(_pm)
  tokenContract.calculateTokensReceived(pm_)
    .then((result) => {
      if (result[0].toString() > 0) {
        let rest = Math.round(web3js.fromWei(result[0].toString()) * 10000) / 10000
        console.log('CalculateTokensReceived:' + rest)
        document.getElementById('buy_calculate').value = '= ' + rest + ' ' + Store.getters.getTokenSymbol // refs
      }
    })
}

// 
function calculateEthereumReceived (_pm) {
  // 
  if (_pm < Store.getters.getSellMinimum) {
    alert('greater than or equal to 1 ' + Store.getters.getTokenSymbol)
    return
  }
  // 
  if (_pm > Store.getters.getTotalToken) {
    alert('Is not greater than total tokens ' + Store.getters.getTotalToken)
    return
  }
  let pm_ = web3js.toWei(_pm)
  tokenContract.calculateEthereumReceived(pm_)
    .then((result) => {
      if (result[0].toString() > 0) {
        let rest = Math.round(web3js.fromWei(result[0].toString()) * 10000) / 10000
        console.log('calculateEthereumReceived:' + rest)
        document.getElementById('sell_calculate').value = '= ' + rest + ' eth' // refs
      }
    })
}

