<script>
import myTitle from '../components/TitleEx'
import myFoot from '../components/FootEx'
import tokenHeadVM from './tokenHead'
import tokenHeadjs from '../js/tokenHead'
import tokenBasejs from '../js/tokenBase'
import router from '@/router/index.js'
export default {
  name: 'tokenBody',
  components: {myTitle, myFoot, tokenHeadVM},
  data: function () {
    return {
      buyInput: '',
      sellInput: '',
      dividendInput: '',
      dividendPerShare: 0,
      tokens: ['SS', 'WPR', 'EVX', 'COVA', 'LAMB', 'FTI'], // token, tokensymbol token
      sellPriceIndexShow: ['sell9', 'sell8', 'sell7', 'sell6', 'sell5', 'sell4', 'sell3', 'sell2', 'sell1'], // 
      buyPriceIndexShow: ['buy9', 'buy8', 'buy7', 'buy6', 'buy5', 'buy4', 'buy3', 'buy2', 'buy1'], // 
      pageIndex: 1,
      pageSize: 6,
      myTradingRecord: {
        tableData: [],
        tableColumns: [
          {field: 'time', title: '', titleAlign: 'center', columnAlign: 'center', width: 120, isResize: true},
          {field: 'token', title: '', titleAlign: 'center', columnAlign: 'center', width: 60, isResize: true},
          {field: 'action', title: '', titleAlign: 'center', columnAlign: 'center', width: 60, isResize: true},
          {field: 'price', title: '(ETH)', titleAlign: 'center', columnAlign: 'center', width: 60, isResize: true},
          {field: 'amount',
            title: '(ETH)',
            titleAlign: 'center',
            columnAlign: 'center',
            width: 60,
            isResize: true,
            formatter:
              function (rowData, rowIndex, pagingIndex, field) {
                return rowData.amount > 0 ? Math.round(rowData.amount * 10000) / 10000 : ''
              }
          },
          {field: 'qty', title: '', formatter: function (rowData, rowIndex, pagingIndex, field) { return rowData.qty > 0 ? Math.round(rowData.qty * 10000) / 10000 : '' }, titleAlign: 'center', columnAlign: 'center', width: 60, isResize: true},
          {field: 'myTokens', title: '', formatter: function (rowData, rowIndex, pagingIndex, field) { return rowData.myTokens > 0 ? Math.round(rowData.myTokens * 10000) / 10000 : '' }, titleAlign: 'center', columnAlign: 'center', width: 60, isResize: true},
          {field: 'myDividends', title: '(ETH)', formatter: function (rowData, rowIndex, pagingIndex, field) { return rowData.myDividends > 0 ? Math.round(rowData.myDividends * 10000) / 10000 : '' }, titleAlign: 'center', columnAlign: 'center', width: 60, isResize: true},
          {field: 'status', title: '', titleAlign: 'center', columnAlign: 'center', width: 60, isResize: true}
        ]
      }
    }
  },
  computed: {
    contractStatusShow: function () {
      if (this.$store.getters.getOnlyAmbassadors) {
        return ''
      } else {
        return ''
      }
    },
    purchaseLimitShow: function () {
      return Math.round(this.$store.state.tokens.tokenMetadata.purchaseLimit / 10000)
    },
    dividendFeeShow: function () {
      let res = 0
      if (this.$store.state.tokens.tokenMetadata.dividendFee > 0) {
        res = Math.round((100 / this.$store.state.tokens.tokenMetadata.dividendFee) * 100) / 100
      }
      return res
    },
    referrerFeeShow: function () {
      let res = 0
      if (this.$store.state.tokens.tokenMetadata.referrerFee > 0) {
        let dividenFee = Math.round((100 / this.$store.state.tokens.tokenMetadata.dividendFee) * 100) / 100
        let referrerFeeBydividendFee = Math.round((this.$store.state.tokens.tokenMetadata.referrerFee) * 100) / 100
        res = Math.round((((100 * dividenFee) / referrerFeeBydividendFee) / 100) * 100) / 100
      }
      return res
    }
  },
  filters: {
    // 4 
    numFilterFixed4 (value) {
      let rest = Math.round(value * 10000) / 10000
      return rest
    }
  },
  watch: {
    //  token
    // '$route' () {
    //   this.$router.go(0)
    // },
    buyInput: function () {
      // 4
      this.buyInput = (this.buyInput.match(/^\d*(\.?\d{0,4})/g)[0]) || null
    },
    sellInput: function () {
      if (this.sellInput.substring(0, 1) !== '0') { // 0
        this.sellInput = this.sellInput.replace(/[^\d]/g, '') // 
      } else {
        this.sellInput = ''
      }
    },
    dividendInput: function () {
      this.dividendInput = (this.dividendInput.match(/^\d*(\.?\d{0,4})/g)[0]) || null
      if (this.$store.getters.getTotalToken > 0 && this.dividendInput > 0) {
        this.dividendPerShare = this.dividendInput / this.$store.getters.getTotalToken
      }
    }
  },
  beforeCreate () {
    document.querySelector('body').setAttribute('style', 'margin: 0; padding:0;')
  },
  // 
  beforeRouteUpdate (to, from, next) {
    if (to.name === from.name && to.query !== from.query) {
      let token_, by_
      token_ = to.query.token
      this.initTokenData(token_, by_)
      next()
    } else {
      next()
    }
  },
  mounted () {
    // http://localhost:8080/tokenBody?token=SS2&ref=0x342d343eF....
    // let token_, by_
    // // token
    // if (typeof this.$route.query.token !== 'undefined') {
    //   token_ = this.$route.query.token
    // } else {
    //   // tokentoken
    //   token_ = this.$store.getters.getSysTokenSymbol
    // }
    // // 
    // if (typeof this.$route.query.by !== 'undefined' && window.web3.isAddress(this.$route.query.by)) {
    //   by_ = this.$route.query.by
    // }

    tokenHeadjs.initContract(this.tokens) // tokens

    this.initTokenData(this.$route.query.token, this.$route.query.by)
  },
  // 
  methods: {
    initTokenData (token, refBy) {
      //  
      tokenBasejs.accountClear()
      //  
      tokenBasejs.tokenClear()

      // token
      if (typeof token !== 'undefined') {
        this.$store.commit('setTokenSymbol', token)
      } else {
        this.$store.commit('setTokenSymbol', this.$store.getters.getSysTokenSymbol)
      }

      // 
      if (typeof refBy !== 'undefined' && tokenBasejs.web3js.isAddress(refBy)) {
        this.$store.commit('setReferredBy', refBy)
      }

      console.log(this.$options.name, 'log in token : ', token, 'referredBy', refBy)

      tokenBasejs.initContract(this.$store.getters.getTokenSymbol) // token

      // token
      this.getTokenProject()

      // 
      this.getNotice()
    },
    tokenHeadListonClick (token) {
      this.$router.push({path: '/token', query: {token: token}})
      // window.location.href = '/token?token=' + token
    },
    getTradingTableData () {
      // // , 
      // let localTradingList = []
      // for (var i = 0; i < localStorage.length; i++) {
      //   var key = localStorage.key(i)
      //   // localStorageTradingRecordkey
      //   if (key.indexOf('TradingRecord') > -1) {
      //     let keyValue = JSON.parse(localStorage.getItem(key))
      //     localTradingList.unshift({'time': keyValue.time, 'address': keyValue.address, 'type': keyValue.type, 'action': keyValue.action, 'amount': keyValue.amount, 'qty': keyValue.qty, 'state': keyValue.state}) // 
      //   }
      // }
      // console.log('getTradingTableData', this.$store.state.tokens.myTradingList)
      // if (localTradingList.length > -1) {
      //   this.$store.state.tokens.myTradingList = localTradingList.slice(
      //     (this.pageIndex - 1) * this.pageSize,
      //     this.pageIndex * this.pageSize
      //   )
      //   this.total = localTradingList.length
      // }

      // console.log('network', this.$store.getters.getNetwork, this.$store.getters.getCustomerAddress)
      // if (this.$store.getters.getNetwork !== '0') {
      //   // 
      //   this.axios.get('/api/EXTrade?account=' + this.$store.getters.getCustomerAddress, { emulateJSON: true })
      //     .then((response) => {
      //       if (response.data.length > 0 && response.data.length < 100) {
      //         //  
      //         this.$store.state.tokens.myTradingForTable.splice(0, this.$store.state.tokens.myTradingForTable.length)
      //         console.log('getTradingTableData1', response.data.length)
      //         for (let i = 0; i < response.data.length; i++) {
      //           let row = response.data[i]
      //           let record = {'sysID': row.sysID, 'token': row.token, 'time': row.time, 'account': row.account, 'type': 'load', 'action': row.action, 'price': row.price, 'amount': row.amount, 'qty': row.qty, 'status': row.status, 'hash': row.hash}
      //           this.$store.commit('setMyTradingForTable', record)
      //         }
      //       }
      //     })

      // console.log('getTradingTableData', this.$store.state.tokens.myTradingList.length, this.$store.state.tokens.myTradingForTable.length)
      if (this.$store.state.tokens.myTradingList.length > 0) {
        // this.$store.state.tokens.myTradingList.sort(function (a, b) {
        //   return b.sysID - a.sysID
        // })
        // this.$store.state.tokens.myTradingList.sort()
        this.$store.state.tokens.myTradingForTable = this.$store.state.tokens.myTradingList.slice(
          (this.pageIndex - 1) * this.pageSize, this.pageIndex * this.pageSize)
        this.$store.state.tokens.myTradingLengthForTable = this.$store.state.tokens.myTradingList.length
      }
    },
    columnCellClass (rowIndex, columnName, rowData) {
      if (rowData.amount > 10) {
        return 'bets-isget-false'
      }
      if (columnName === 'action') {
        return 'bets-isget-false'
      }
      if (rowData.action === '' && columnName === 'action') {
        return 'bets-winA-GreaterThanZero'
      }
    },
    pageChange (pageIndex) {
      this.pageIndex = pageIndex
      this.getTradingTableData()
    },
    calculateTokensReceived () {
      var inputValue = this.buyInput
      tokenBasejs.calculateTokensReceived(inputValue)
    },
    // 
    calculateEthereumReceived () {
      var inputValue = this.sellInput
      tokenBasejs.calculateEthereumReceived(inputValue)
    },
    Buy () {
      var buyQty = this.buyInput
      tokenBasejs.buy(buyQty)
    },
    Sell () {
      var sellQty = this.sellInput
      tokenBasejs.sell(sellQty)
    },
    getTokenProject () {
      let token = this.$store.getters.getTokenSymbol
      this.$http.get('/api/token?token=' + token, {emulateJSON: true})
        .then((response) => {
          if (response.data.length > 0) {
            let tokenProject = {
              'sysID': response.data[0].sysID,
              'sysTime': response.data[0].sysTime,
              'company': response.data[0].company,
              'tokenFullName': response.data[0].tokenFullName,
              'token': response.data[0].token,
              'linkMan': response.data[0].linkMan,
              'telephone': response.data[0].telephone,
              'otherContacts': response.data[0].otherContacts,
              'introduce': response.data[0].introduce,
              'status': response.data[0].status,
              'tag': response.data[0].tag,
              'website': response.data[0].website,
              'whiteBook': response.data[0].whiteBook,
              'listedTime': response.data[0].listedTime
            }
            this.$store.commit('setTokenProject', tokenProject)
          }
        })
    },
    getNotice () {
      let token = this.$store.getters.getTokenSymbol
      this.$http.get('/api/EXNotice?initiator=' + token, {emulateJSON: true})
        .then((response) => {
          if (response.data.length > 0) {
            for (let i = 0; i < response.data.length; i++) {
              let notice = {'sysID': response.data[i].sysID, 'sysTime': response.data[i].sysTime, 'title': response.data[i].title, 'initiator': response.data[i].initiator, 'content': response.data[i].content}
              this.$store.commit('setNoticeList', notice)
            }
          }
        })
